import { useState } from "react";
import {
    formatShortDate,
    formatPerformance,
    formatPosition,
} from "../../formatters";
import { Performance, Venue } from "../../types";
import "./athletePagePerformanceRow.css";

const AthletePagePerformanceRow = ({
    performance,
    eventName,
    venue,
}: {
    performance: Performance;
    eventName?: string;
    venue?: Venue;
}) => {
    const [isExpanded, setIsExpanded] = useState(false);
    return (
        <>
            <div
                className={`performance-row ${isExpanded ? "selected" : ""}`}
                onClick={() => setIsExpanded(!isExpanded)}
            >
                <div className="event">{eventName}</div>
                <div className="meeting">{performance.meeting}</div>
                <div className="performance">
                    {formatPerformance(performance.performance)}
                </div>
                <div className="date">{formatShortDate(performance.date)}</div>
            </div>
            <div
                className={`performance-row-expanded ${
                    isExpanded ? "" : "hidden"
                }`}
            >
                <div className="expanded-statistics">
                    <div className="statistic-row">
                        <div>Venue:</div>
                        <div>{venue?.name}</div>
                    </div>
                    <div className="statistic-row">
                        <div>Position:</div>
                        <div>{formatPosition(performance.position)}</div>
                    </div>
                    {performance.heat !== "" && (
                        <div className="statistic-row">
                            <div>Heat:</div>
                            <div>{performance.heat}</div>
                        </div>
                    )}
                    {performance.wind !== 0 && (
                        <div className="statistic-row">
                            <div>Wind:</div>
                            <div>{performance.wind}</div>
                        </div>
                    )}
                </div>
            </div>
        </>
    );
};

export default AthletePagePerformanceRow;
