import { formatPercentage } from "../../../formatters";
import "./headToHeadRow.css";

interface HeadToHeadRowProps {
    name: string;
    a: number;
    b: number;
    formattedA?: string;
    formattedB?: string;
    formatValueAs?: "percentage";
}

const getFormattedValue = (value: number, formatter?: string) => {
    if (formatter === "percentage") {
        return formatPercentage(value);
    } else {
        return value;
    }
};

const HeadToHeadRow = ({
    name,
    a,
    b,
    formattedA,
    formattedB,
    formatValueAs,
}: HeadToHeadRowProps) => {
    const aBarWidth = formatValueAs === "percentage" ? a : (a / (a + b)) * 100;
    const bBarWidth = formatValueAs === "percentage" ? b : (b / (a + b)) * 100;
    return (
        <div className="head-to-head-row">
            <div className="value-name">{name}</div>
            <div className="bars">
                <div className="bar-container">
                    <div
                        className="remainder"
                        style={{ width: `${100 - aBarWidth}%` }}
                    ></div>
                    <div
                        className="bar"
                        style={{ width: `${aBarWidth}%` }}
                    ></div>
                </div>
                <div className="bar-divider"></div>
                <div className="bar-container">
                    <div
                        className="bar"
                        style={{
                            width: `${bBarWidth}%`,
                            backgroundColor: "#6347ff",
                        }}
                    ></div>
                    <div
                        className="remainder"
                        style={{ width: `${100 - bBarWidth}%` }}
                    ></div>
                </div>
            </div>
            <div className="values">
                <div className="value">
                    {formattedA ?? getFormattedValue(a, formatValueAs)}
                </div>
                <div className="value">
                    {formattedB ?? getFormattedValue(b, formatValueAs)}
                </div>
            </div>
        </div>
    );
};

export default HeadToHeadRow;
