import { EventStatistics, Performance, Event } from "./types";
import * as constants from "./constants";

export const isSameRace = (a: Performance, b: Performance) =>
    a.meetingId === b.meetingId && a.eventId === b.eventId && a.heat === b.heat;

const getLongestWinStreak = (performances: Performance[]): number => {
    let max = 0;
    let current = 0;
    for (let i = 0; i < performances.length; i++) {
        if (performances[i].position === 1) {
            current++;
            if (current > max) {
                max = current;
            }
        } else {
            current = 0;
        }
    }
    return max;
};

const getMostPopularVenueId = (performances: Performance[]): string => {
    const venueCounts = performances.reduce(
        (acc: Record<string, number>, p) => ({
            ...acc,
            [p.venueId]: (acc[p.venueId] ?? 0) + 1,
        }),
        {}
    );
    let mostPopularVenueId = "";
    let max = 0;
    Object.entries(venueCounts).forEach(([id, count]) => {
        if (count > max) {
            max = count;
            mostPopularVenueId = id;
        }
    });
    return mostPopularVenueId;
};

const getWinCount = (performances: Performance[]) =>
    performances.reduce((acc, p) => (p.position === 1 ? acc + 1 : acc), 0);

const getWinRate = (performances: Performance[]) =>
    (getWinCount(performances) / performances.length) * 100;

const getTopThreeRate = (performances: Performance[]) =>
    (performances.reduce((acc, p) => (p.position < 4 ? acc + 1 : acc), 0) /
        performances.length) *
    100;

const getBestPerformance = (performances: Performance[], event?: string) =>
    isTimedPerformance(event)
        ? Math.min(
              ...performances
                  .filter(({ performance }) => !Number.isNaN(performance))
                  .map((performance) => performance.performance)
          )
        : Math.max(
              ...performances
                  .filter(({ performance }) => !Number.isNaN(performance))
                  .map((performance) => performance.performance)
          );

const getSeasonBestPerformance = (
    performances: Performance[],
    event?: string
) => {
    const seasonPerformances = performances.filter(
        (p) => p.date >= new Date(new Date().getFullYear(), 0, 1).valueOf()
    );
    return seasonPerformances.length
        ? getBestPerformance(seasonPerformances, event)
        : undefined;
};

export const getEventStatistics = (
    performancesByEvent: Record<string, Performance[]>,
    events: Event[]
): Record<string, EventStatistics> => {
    const eventIds = Object.keys(performancesByEvent);
    if (!eventIds.length) {
        return {};
    }
    return eventIds.reduce(
        (acc: Record<string, EventStatistics>, eventId) => ({
            ...acc,
            [eventId]: {
                best: getBestPerformance(
                    performancesByEvent[eventId],
                    events.find((e) => e.id === eventId)?.name
                ),
                seasonBest: getSeasonBestPerformance(
                    performancesByEvent[eventId],
                    events.find((e) => e.id === eventId)?.name
                ),
                longestWinStreak: getLongestWinStreak(
                    performancesByEvent[eventId]
                ),
                mostPopularVenueId: getMostPopularVenueId(
                    performancesByEvent[eventId]
                ),
                winCount: getWinCount(performancesByEvent[eventId]),
                winRate: getWinRate(performancesByEvent[eventId]),
                topThreeRate: getTopThreeRate(performancesByEvent[eventId]),
                totalPerformances: performancesByEvent[eventId].length,
            },
        }),
        {}
    );
};

export const getBestPerformanceProgression = (
    performances: Performance[],
    event: string
) => {
    let currentBest = isTimedPerformance(event) ? 9999999 : 0;
    const test = performances
        .sort((a, b) => a.date - b.date)
        .filter((p) => !Number.isNaN(p.performance))
        .reduce((acc: any, p) => {
            if (
                isTimedPerformance(event)
                    ? p.performance < currentBest
                    : currentBest < p.performance
            ) {
                const lastBest = currentBest;
                currentBest = p.performance;

                return acc.length
                    ? [...acc, [p.date, lastBest], [p.date, currentBest]]
                    : [...acc, [p.date, currentBest]];
            }
            return acc;
        }, []);
    const final = [Date.now(), test[test.length - 1][1]];
    test.push(final);
    return test;
};

export const getSeasonPerformanceProgression = (
    performances: Performance[],
    event: string
) => {
    const seasons: [number, number][][] = [];
    let currentBest = isTimedPerformance(event) ? 9999999 : 0;
    let bestDate = 0;
    performances
        .sort((a, b) => a.date - b.date)
        .filter((p) => !Number.isNaN(p.performance))
        .forEach((p) => {
            if (
                new Date(p.date).getFullYear() >
                new Date(bestDate).getFullYear()
            ) {
                if (seasons.length) {
                    seasons[seasons.length - 1].push([
                        new Date(
                            new Date(p.date).getFullYear(),
                            1,
                            1
                        ).valueOf(),
                        currentBest,
                    ]);
                }
                seasons.push([[p.date, p.performance]]);
                currentBest = p.performance;
                bestDate = p.date;
            }
            if (
                isTimedPerformance(event)
                    ? p.performance < currentBest
                    : currentBest < p.performance
            ) {
                const lastBest = currentBest;
                currentBest = p.performance;
                bestDate = p.date;
                // push a "bump" perf in
                seasons[seasons.length - 1].push([p.date, lastBest]);
                seasons[seasons.length - 1].push([p.date, p.performance]);
            }
        });
    return seasons;
};

export const getPerformancesByEvent = (performances: Performance[]) =>
    performances.reduce((acc: Record<string, Performance[]>, p) => {
        const updatedEntry = acc[p.eventId] ? [p, ...acc[p.eventId]] : [p];
        return {
            ...acc,
            [p.eventId]: updatedEntry,
        };
    }, {});

export const isTimedPerformance = (event?: string) => {
    if (event === undefined) {
        return true;
    }
    const stripped = event.match(/\d+/);
    if (stripped) {
        return !Number.isNaN(stripped[0]);
    }
    return constants.TIMED_EVENTS.includes(event);
};

export const isCurrentYear = (year: string | number) =>
    new Date().toLocaleString(undefined, { year: "numeric" }) === String(year);

const getMedalFromPerformance = (performance: Performance) => {
    if (performance.heat === "") {
        switch (performance.position) {
            case 1:
                return constants.MEDAL.GOLD;
            case 2:
                return constants.MEDAL.SILVER;
            case 3:
                return constants.MEDAL.BRONZE;
            default:
                return null;
        }
    }
    return null;
};

export const getChampionshipMedals = (performances: Performance[]) => {
    return Object.entries(constants.CHAMPIONSHIPS).reduce(
        (
            acc: Record<string, constants.MEDAL[]>,
            [championship, meetingIds]
        ) => {
            performances.forEach((p) => {
                if (meetingIds.includes(p.meetingId)) {
                    const medal = getMedalFromPerformance(p);
                    if (medal !== null) {
                        if (acc?.[championship]) {
                            acc[championship].push(medal);
                        } else {
                            acc[championship] = [medal];
                        }
                    }
                }
            });
            return acc;
        },
        [] as unknown as Record<string, constants.MEDAL[]>
    );
};

export const getDatePo10AsSearchParam = (offset: number = 0) => {
    const d = new Date(Date.now() - offset);
    return `${d.getDate()}-${d.toLocaleString("default", {
        month: "short",
    })}-${d.getFullYear()}`;
};
