import { load } from "cheerio";
import {
    Athlete,
    Event,
    Meeting,
    Performance,
    Ranking,
    AthleteSearchResult,
    Venue,
    MeetingSearchResult,
} from "./types";
import * as urls from "./urls";
import { getDatePo10AsSearchParam } from "./queries";
import { MILLIS } from "./constants";

const getPo10AthletePage = async (athleteId: string) => {
    try {
        const res = await fetch(`${urls.replAthleteURL}${athleteId}`, {
            mode: "cors",
            headers: {
                // "Content-Type": "application/json",
                "Access-Control-Allow-Origin": "*",
            },
        }).then((page) => page.text());
        return res;
    } catch (error) {
        console.log(error);
    }
};

const getPo10MeetingPage = async (meetingId: string, pageNumber: number) => {
    try {
        const res = await fetch(
            `${urls.replMeetingURL}${meetingId}?pagenum=${pageNumber}`,
            {
                mode: "cors",
                headers: {
                    // "Content-Type": "application/json",
                    "Access-Control-Allow-Origin": "*",
                },
            }
        ).then((page) => page.text());
        return res;
    } catch (error) {
        console.log(error);
    }
};

const getAthleteSearchPage = async (
    surname: string,
    firstname: string,
    club: string
) => {
    try {
        const res = await fetch(
            `${urls.replAthleteSearchURL}?surname=${surname}&firstname=${firstname}&club=${club}`,
            {
                mode: "cors",
                headers: {
                    "Access-Control-Allow-Origin": "*",
                },
            }
        ).then((page) => page.text());
        return res;
    } catch (error) {
        console.log(error);
    }
};

const getMeetingSearchPage = async () => {
    try {
        const res = await fetch(
            `${urls.replMeetingSearchURL}?datefrom=${getDatePo10AsSearchParam(
                MILLIS.MONTHS_6
            )}&dateto=${getDatePo10AsSearchParam()}`,
            {
                mode: "cors",
                headers: {
                    "Access-Control-Allow-Origin": "*",
                },
            }
        ).then((page) => page.text());
        return res;
    } catch (error) {
        console.log(error);
    }
};

