export const getAthleteSearchResults = async (
    surname: string,
    firstname: string,
    club: string
) => {
    const page = await getAthleteSearchPage(surname, firstname, club)
        .then((searchPage) => searchPage)
        .catch((error) => console.log(error));
    if (page === undefined) {
        return [];
    }

    const $ = load(page);

    const isAthletePage = $($("title")).text() === "\n\tAthlete Profile\n";

    if (isAthletePage) {
        const prefetchedId =
            $("form")?.attr("action")?.split("athleteid=")[1] ?? "";
        const data = await getAthleteData(prefetchedId, [], [], [], page);
        return data;
    }

    const searchResults: AthleteSearchResult[] = [];

    let rows = $("#cphBody_dgAthletes").find("tr");

    rows.each((j, row) => {
        // skip the table header row
        if (j === 0) {
            return true;
        }

        const cells = $(row).find("td");

        const rowData = {
            id: "",
            name: "",
            ageGroup: "",
            club: "",
            sex: "",
        };

        cells.each((i, cell) => {
            switch (i) {
                case 0:
                    rowData.name = $(cell).text();
                    break;
                case 2:
                    rowData.ageGroup = $(cell).text();
                    break;
                case 1:
                    rowData.name = `${rowData.name} ${$(cell).text()}`;
                    break;
                case 5:
                    rowData.sex = $(cell).text();
                    break;
                case 6:
                    rowData.club = $(cell).text();
                    break;
                case 7:
                    rowData.id = parseAthleteIdFromLink($(cell).html() ?? "");
                    break;
                default:
                    break;
            }
        });
        searchResults.push(rowData);
    });

    return searchResults;
};

export const getMeetingSearchResults = async () => {
    const page = await getMeetingSearchPage()
        .then((searchPage) => searchPage)
        .catch((error) => console.log(error));

    if (page === undefined) {
        return [];
    }

    const $ = load(page);

    const searchResults: MeetingSearchResult[] = [];

    let rows = $("#cphBody_dgMeetings").find("tr");

    rows.each((j, row) => {
        // skip the table header row
        if (j === 0) {
            return true;
        }

        const cells = $(row).find("td");

        const rowData = {
            id: "",
            date: 0,
            name: "",
            venueName: "",
            type: "Track",
        };

        cells.each((i, cell) => {
            switch (i) {
                case 0:
                    rowData.date = parseLongDateFromString($(cell).text());
                    break;
                case 1:
                    rowData.name = $(cell).text().trim();
                    break;
                case 2:
                    rowData.venueName = $(cell).text();
                    break;
                case 3:
                    rowData.type = $(cell).text();
                    break;
                case 5:
                    rowData.id = parseMeetingIdFromSubmitResultLink(
                        $(cell).html() ?? ""
                    );
                    break;
                default:
                    break;
            }
        });
        searchResults.push(rowData);
    });

    return searchResults;
};

export const getMeetingData = async (
    meetingId: string,
    currentEvents: Event[],
    pageNumber: number,
    athleteIds: string[] = []
) => {
    let performances: Performance[] = [];
    let athletes: Athlete[] = [];
    // let athleteIds: string[] = athleteIds;
    // const meetingDetails: Record<string, string | number> = {};
    const page = await getPo10MeetingPage(meetingId, pageNumber)
        .then((meetingPage) => meetingPage)
        .catch((error) => console.log(error));
    if (page === undefined) {
        return null;
    }
    const $ = load(page);

    const foundNumOfPages = parseInt(
        $("#cphBody_lblTopPageLinks").find("a").last().text()
    );

    const numberOfPages = Number.isNaN(foundNumOfPages) ? 1 : foundNumOfPages;

    const generalDetails = $("#cphBody_lblMeetingDetails")
        .html()
        ?.split("<br>");

    const details = {
        id: meetingId,
        name: $("#cphBody_lblMeetingDetails").find("b").text(),
        date: generalDetails ? generalDetails[2] : "",
        location: generalDetails ? generalDetails[1] : "",
    };

    // column index locations for data
    const columnDataIndex: Record<string, number | undefined> = {
        Pos: 0,
        Perf: undefined,
        Name: undefined,
        AG: undefined,
        Coach: undefined,
        Club: undefined,
        SB: undefined,
        PB: undefined,
    };

    let rows = $("#cphBody_dgP").find("tr");

    let events = currentEvents;
    let eventData: { id: string; events: Event[] };
    let heat: string;
    let eventName;
    let classification: string;
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    let shortDate;

    rows.each((j, row) => {
        const cells = $(row).find("td");

        // meetings have race table header with information
        // take this info and apply to the table's performances
        const raceHeaderRow = $(cells[0]).find("a");
        const raceHeaderEmptyLinkCell = $(raceHeaderRow)
            .attr("name")
            ?.includes("r");
        if (raceHeaderEmptyLinkCell) {
            const raceHeaderContent = $(cells[0]).find("b").text();
            [eventName, classification, heat, shortDate] =
                raceHeaderContent.split(" ");
            if (raceHeaderContent.split(" ").length === 4) {
                heat = "";
            }
            eventData = parseEventFromString(eventName, events);
            events = eventData.events;
        }
        const performance: Performance = {
            athleteId: "",
            eventId: eventData?.id ?? "",
            performance: 0,
            tags: [],
            wind: 0,
            position: 0,
            heat: heat ?? "",
            venueId: "",
            meetingId: meetingId,
            meeting: "",
            date: 0,
            classification: classification,
        };
        const athlete: Athlete = {
            id: "",
            name: "",
            club: "",
            sex: "",
            ageGroup: "",
            county: "",
            region: "",
            nation: "",
        };

        // find row that has "Pos" as first cell
        // this row gives locations of data cells
        // i.e. find cell positions for the following and assign column number to get data
        // "Pos","Perf","AG","Coach","Club","SB","PB"
        // find columns for data
        // check if we are on a table column header row
        if ($(cells[0]).text() === "Pos") {
            for (let colIndex = 0; colIndex < cells.length; colIndex++) {
                switch ($(cells[colIndex]).text()) {
                    case "Perf":
                        columnDataIndex.Perf = colIndex;
                        break;
                    case "Name":
                        columnDataIndex.Name = colIndex;
                        break;
                    case "AG":
                        columnDataIndex.AG = colIndex;
                        break;
                    case "Coach":
                        columnDataIndex.Coach = colIndex;
                        break;
                    case "Club":
                        columnDataIndex.Club = colIndex;
                        break;
                    case "SB":
                        columnDataIndex.SB = colIndex;
                        break;
                    case "PB":
                        columnDataIndex.PB = colIndex;
                        break;
                }
            }
        }
        // skip non performance rows
        if (
            !(
                $(cells[1]).text() === "DNF" ||
                $(cells[1]).text() === "DNS" ||
                $(cells[1]).text() === "DQ" ||
                parseInt($(cells[1]).text())
            )
        ) {
            return true;
        }

        cells.each((i, cell) => {
            // collect data we only know columnDataIndex values for
            switch (i) {
                case columnDataIndex.Pos:
                    performance.position = parseInt($(cell).text());
                    break;
                case columnDataIndex.Perf:
                    performance.performance = parsePerformanceFromString(
                        $(cell).text()
                    );
                    break;
                case columnDataIndex.Name:
                    // if an athlete with no id, just use name as id
                    const aId =
                        $(cell).html() !== $(cell).text()
                            ? parseAthleteIdFromMeetingPageLink(
                                  $(cell).html() ?? ""
                              )
                            : $(cell).text();
                    performance.athleteId = aId;
                    athlete.id = aId;
                    athlete.name = $(cell).text();
                    // if name has Para tag, e.g. T20 ,tag as para
                    // a digit in the name shows this potentially
                    if (/\d/.test($(cell).text())) {
                        performance.tags.push("para");
                    }
                    break;
                case columnDataIndex.AG:
                    athlete.ageGroup = $(cell).text();
                    break;
                case columnDataIndex.Club:
                    athlete.club = $(cell).text();
                    break;
            }
        });
        performances.push(performance);
        if (!athleteIds.includes(athlete.id)) {
            athletes.push(athlete);
        }
    });
    return { performances, athletes, events, details, numberOfPages };
};

export const getAthleteData = async (
    athleteId: string,
    currentEvents: Event[],
    currentMeetings: Meeting[],
    currentVenues: Venue[],
    prefetchedPage?: string
) => {
    let performances: Performance[] = [];
    const rankings: Ranking[] = [];
    // load these in from file
    let venues = currentVenues;
    let events = currentEvents;
    let meetings = currentMeetings;
    // get page from website
    const page = prefetchedPage
        ? prefetchedPage
        : await getPo10AthletePage(athleteId)
              .then((athletePage) => athletePage)
              .catch((error) => console.log(error));
    if (page === undefined) {
        return null;
    }
    const $ = load(page);

    // scrape page for athlete details
    // @ts-ignore
    const athlete: Athlete = {};
    const dataList: string[] = [];
    $("#cphBody_pnlAthleteDetails")
        .find("td")
        .each((i, element) => {
            if (
                $(element).html()?.replace("&amp;", "&") === $(element).text()
            ) {
                dataList.push($(element).text());
            }
        });

    athlete["id"] = athleteId.toString();
    athlete["name"] = $("h2").first().text().trim();

    if (dataList[0] === "Yes") {
        athlete["club"] = dataList[1];
        athlete["sex"] = dataList[2];
        athlete["ageGroup"] = dataList[3];
        athlete["county"] = dataList[4];
        athlete["region"] = dataList[5];
        athlete["nation"] = dataList[6];
    } else {
        athlete["club"] = dataList[0];
        athlete["sex"] = dataList[1];
        athlete["ageGroup"] = dataList[2];
        athlete["county"] = dataList[3];
        athlete["region"] = dataList[4];
        athlete["nation"] = dataList[5];
    }

    // scrape page for performances
    let rows = $("#cphBody_pnlPerformances")
        .find(".alternatingrowspanel")
        .first()
        .find("tr");

    const rankingRows = $("#cphBody_pnlMain")
        .find(".alternatingrowspanel")
        .last()
        .find("tr");

    // go through each row on the performances table and create a performance object to relate
    rows.each((j, row) => {
        const cells = $(row).find("td");
        // skip those rows which are season headings
        if ($(cells[0]).html() !== $(cells[0]).text()) {
            return true;
        }
        const performance: Performance = {
            athleteId: athleteId.toString(),
            eventId: "",
            performance: 0,
            tags: [],
            wind: 0,
            position: 0,
            heat: "",
            venueId: "",
            meetingId: "",
            meeting: "",
            date: 0,
        };
        let meetingId;
        // go through each cell in the row and add that to a performance object
        cells.each((i, cell) => {
            switch (i) {
                case 0:
                    const eventData = parseEventFromString(
                        $(cell).text(),
                        events
                    );
                    performance.eventId = eventData.id;
                    events = eventData.events;
                    break;
                case 1:
                    performance.performance = parsePerformanceFromString(
                        $(cell).text()
                    );
                    break;
                case 2:
                    performance.tags = $(cell).text().split("");
                    break;
                case 3:
                    performance.wind = parseWindReading($(cell).text());
                    break;
                case 5:
                    performance.position = parseInt($(cell).text());
                    break;
                case 9:
                    // get meetingId and venueId from the link in the "Venue" column
                    meetingId = parseMeetingIdFromLink($(cell).html() ?? "");
                    performance.meetingId = meetingId;
                    const venueData = parseVenueFromString(
                        $(cell).text(),
                        performance.tags,
                        venues
                    );
                    performance.venueId = venueData.id;
                    venues = venueData.venues;
                    break;
                case 11:
                    performance.date = parseDateFromString($(cell).text());
                    break;
                case 6:
                    if ($(cell).text()) {
                        performance.heat = $(cell).text();
                    }
                    break;
                case 10:
                    performance.meeting = $(cell).text();
                    // if new meeting is added, this is the actual meeting name to add, not the venue id
                    meetings = handleMeetingCreation(
                        performance.meetingId,
                        $(cell).text(),
                        meetings
                    );
                    break;
                default:
                    break;
            }
        });
        performances.push(performance);
    });

    // go through each ranking row and build list of "Ranking" objects
    rankingRows.each((j, row) => {
        const cells = $(row).find("td");

        // skip table headers
        if (j === 0) {
            return true;
        }

        let eventId = "";
        let year = "";
        let rank = 0;
        let ageGroup = "";

        cells.each((i, cell) => {
            if (i === 0) {
                let eventName = $(cell).text();
                if (
                    eventName.slice(eventName.length - 1, eventName.length) ===
                    "i"
                ) {
                    return true;
                }
            }
            switch (i) {
                case 0:
                    let eventName = $(cell).text();
                    eventId =
                        events.find((e) => e.name === eventName)?.id ?? "";
                    break;
                case 2:
                    ageGroup = $(cell).text();
                    break;
                case 3:
                    year = $(cell).text();
                    break;
                case 4:
                    rank = parseInt($(cell).text());
                    break;
                default:
                    break;
            }
        });
        if (eventId === "") {
            return true;
        }
        rankings.push({
            athleteId,
            eventId,
            year,
            rank,
            ageGroup,
        });
    });
    return {
        athlete: athlete as Athlete,
        performances,
        venues,
        meetings,
        events,
        rankings,
    };
};

const parseDateFromString = (value: string): number => {
    const [day, month, year] = value.split(" ");
    const date = new Date(
        `${day} ${month} ${year.length === 2 ? "20" : ""}${year}`
    );
    return date.valueOf();
};

const parseLongDateFromString = (value: string): number => {
    const dashLocation = value.indexOf("-");
    if (dashLocation > 0) {
        return parseDateFromString(value.split("-")[1].slice(5));
    } else {
        return parseDateFromString(value.slice(4)); // remove the day from string
    }
};

const parsePerformanceFromString = (performance: string): number => {
    let value = 0;
    let splitStrings;
    let rest;
    splitStrings = performance.split(":");
    if (splitStrings.length > 1) {
        value = parseFloat(splitStrings[0]) * 60;
        rest = splitStrings[1];
    } else {
        rest = splitStrings[0];
    }
    const [secs, hundredths] = rest.split(".");
    if (secs) {
        value = value + parseFloat(secs);
    }
    if (hundredths) {
        value = value + parseFloat(`.${hundredths}`);
    }
    return value;
};

const parseVenueFromString = (
    venue: string,
    tags: string[],
    venues: Venue[]
): { id: string; venues: Venue[] } => {
    const isIndoor = tags.includes("i");
    const isAtAtltitude = tags.includes("A");
    const isOversized = tags.includes("+");
    for (let i = 0; i < venues.length; i++) {
        if (
            venues[i].name === venue &&
            venues[i].isIndoor === isIndoor &&
            venues[i].isOversized === isOversized &&
            venues[i].isAtAltitude === isAtAtltitude
        ) {
            return { id: venues[i].id, venues };
        }
    }
    let newVenueId = (venues.length + 1).toString();
    venues = [
        ...venues,
        {
            id: newVenueId,
            name: venue,
            isIndoor: isIndoor,
            isOversized: isOversized,
            isAtAltitude: isAtAtltitude,
        },
    ];
    return { id: newVenueId, venues };
};

const parseEventFromString = (
    event: string,
    events: Event[]
): { id: string; events: Event[] } => {
    for (let i = 0; i < events.length; i++) {
        if (events[i].name === event) {
            return { id: events[i].id, events };
        }
    }
    // add new event to list if not existing
    let newEventId = (events.length + 1).toString();
    events = [...events, { id: newEventId, name: event }];
    return { id: newEventId, events };
};

const handleMeetingCreation = (
    id: string,
    name: string,
    meetings: Meeting[]
): Meeting[] => {
    if (meetings.find((meeting) => meeting.id === id)) {
        return meetings;
    }
    meetings = [...meetings, { id: id, name }];
    return meetings;
};

const parseMeetingIdFromLink = (link: string): string =>
    link.split("meetingid=")[1].split("&amp;")[0];

const parseMeetingIdFromSubmitResultLink = (link: string): string =>
    link.split("meetingid=")[1].split('"')[0];

const parseAthleteIdFromMeetingPageLink = (link: string): string =>
    link.split("athleteid=")[1].split('"')[0];

const parseAthleteIdFromLink = (link: string): string =>
    link.split("athleteid=")[1].split('">Show')[0];

const parseWindReading = (windReading: string): number =>
    windReading === "" ? 0 : parseFloat(windReading);
