import { useEffect, useState } from "react";
import ComparisonRow from "../../components/Rows/HeadToHeadRow/HeadToHeadRow";
import { Athlete, Performance, AthleteSearchResult } from "../../types";
import "./headToHead.css";
import { fetchAthleteData, fetchAthleteSearchResults } from "../../thunks";
import { useSelector } from "react-redux";
import {
    getAthleteById,
    getAthleteIds,
    getEvents,
    getMeetings,
    getPerformances,
    getVenues,
} from "../../selectors";
import { AnalysisState } from "../../slice";
import {
    getAthleteHeadToHead,
    getHeadToHeadPerformances,
} from "../../analysis";
import { getEventStatistics, getPerformancesByEvent } from "../../queries";
import ResultComparisonRow from "../../components/Rows/ResultComparisonRow/ResultComparisonRow";
import { formatPerformance } from "../../formatters";
import Search from "../../components/Search/Search";
import { SearchResultType } from "../../components/Search/Result";

const HeadToHeadPage = () => {
    const [athleteIds, setAthleteIds] = useState<
        Record<string, string | undefined>
    >({
        A: undefined,
        B: undefined,
    });
    const fetchedAthleteIds = useSelector(getAthleteIds);
    const [selectedEvent, setSelectedEvent] = useState("ALL");
    const [showSearch, setShowSearch] = useState(true);
    const allEvents = useSelector(getEvents);
    const allMeetings = useSelector(getMeetings);
    const allVenues = useSelector(getVenues);
    const allPerformances = useSelector(getPerformances);
    const athleteA = useSelector((state: AnalysisState) =>
        getAthleteById(state, athleteIds.A)
    );
    const athleteB = useSelector((state: AnalysisState) =>
        getAthleteById(state, athleteIds.B)
    );
    const [comparisonSheet, setComparisonSheet] = useState<
        Record<string, Record<string, Record<string, number>>>
    >({});
    const [headToHeadPerformances, setHeadToHeadPerformances] = useState<
        Performance[][]
    >([]);
    const handleResultClick = (id: string) => () => {
        if (!fetchedAthleteIds.includes(id)) {
            fetchAthleteData(id, allEvents, allMeetings, allVenues);
        }
        if (athleteIds.A) {
            setAthleteIds({ ...athleteIds, B: id });
            setShowSearch(false);
        } else {
            setAthleteIds({ ...athleteIds, A: id });
        }
    };
    const handleSelectEvent = (event: React.ChangeEvent<HTMLSelectElement>) => {
        setSelectedEvent(event.target.value);
    };
    useEffect(() => {
        if (athleteA && athleteB) {
            setComparisonSheet(
                getAthleteHeadToHead(
                    [athleteA.id, athleteB.id],
                    allPerformances
                )
            );
            setHeadToHeadPerformances(
                getHeadToHeadPerformances(
                    [athleteA.id, athleteB.id],
                    allPerformances
                )
            );
        }
    }, [athleteA, athleteB, allPerformances]);

    const dataToResultFormat = (
        data: AthleteSearchResult[]
    ): SearchResultType[] =>
        data.map((d) => ({
            id: d.id,
            primary: d.name,
            secondary: d.club,
            sub: d.ageGroup,
        }));

    const AthleteInfo = ({ athlete }: { athlete?: Athlete }) =>
        athlete ? (
            <div key={athlete.id} className="athlete-information">
                <div className="name">{athlete.name}</div>
                <div className="sub-details">
                    <div className="age-group">{athlete.ageGroup}</div>
                    <div className="club">
                        {athlete.club.split("/").map((c) => (
                            <div key={c}>{c}</div>
                        ))}
                    </div>
                </div>
            </div>
        ) : (
            <div>Search an athlete</div>
        );

    const ComparisonSection = ({ eventId }: { eventId: string }) => {
        if (
            !(
                athleteA &&
                athleteB &&
                Object.keys(comparisonSheet).length &&
                Object.keys(comparisonSheet[athleteA.id][athleteB.id]).length >
                    0
            )
        ) {
            return null;
        }
        const eventStatsAthleteA = getEventStatistics(
            {
                ...getPerformancesByEvent(
                    allPerformances.filter((p) => p.athleteId === athleteA.id)
                ),
                ALL: allPerformances.filter((p) => p.athleteId === athleteA.id),
            },
            [...allEvents, { id: "ALL", name: "ALL" }]
        );
        const eventStatsAthleteB = getEventStatistics(
            {
                ...getPerformancesByEvent(
                    allPerformances.filter((p) => p.athleteId === athleteB.id)
                ),
                ALL: allPerformances.filter((p) => p.athleteId === athleteB.id),
            },
            [...allEvents, { id: "ALL", name: "ALL" }]
        );
        return (
            eventStatsAthleteA &&
            eventStatsAthleteB && (
                <>
                    {(comparisonSheet[athleteA.id][athleteB.id][eventId] > 0 ||
                        comparisonSheet[athleteB.id][athleteA.id][eventId] >
                            0) && (
                        <ComparisonRow
                            name="Head To Head Wins"
                            a={
                                comparisonSheet[athleteA.id][athleteB.id][
                                    eventId
                                ]
                            }
                            b={
                                comparisonSheet[athleteB.id][athleteA.id][
                                    eventId
                                ]
                            }
                        />
                    )}
                    {eventId !== "ALL" && (
                        <ComparisonRow
                            name="Personal Best"
                            a={eventStatsAthleteA[eventId]?.best}
                            b={eventStatsAthleteB[eventId]?.best}
                            formattedA={formatPerformance(
                                eventStatsAthleteA[eventId]?.best,
                                allEvents.find(({ id }) => id === eventId)?.name
                            )}
                            formattedB={formatPerformance(
                                eventStatsAthleteB[eventId]?.best,
                                allEvents.find(({ id }) => id === eventId)?.name
                            )}
                        />
                    )}
                    {eventStatsAthleteA[eventId]?.seasonBest &&
                        eventStatsAthleteB[eventId]?.seasonBest &&
                        eventId !== "ALL" && (
                            <ComparisonRow
                                name="Season Best"
                                a={eventStatsAthleteA[eventId]?.seasonBest ?? 0}
                                b={eventStatsAthleteB[eventId]?.seasonBest ?? 0}
                                formattedA={formatPerformance(
                                    eventStatsAthleteA[eventId]?.seasonBest ??
                                        0,
                                    allEvents.find(({ id }) => id === eventId)
                                        ?.name
                                )}
                                formattedB={formatPerformance(
                                    eventStatsAthleteB[eventId]?.seasonBest ??
                                        0,
                                    allEvents.find(({ id }) => id === eventId)
                                        ?.name
                                )}
                            />
                        )}
                    <ComparisonRow
                        name="Total Performances"
                        a={eventStatsAthleteA[eventId]?.totalPerformances}
                        b={eventStatsAthleteB[eventId]?.totalPerformances}
                    />
                    <ComparisonRow
                        name="Win Count"
                        a={eventStatsAthleteA[eventId]?.winCount}
                        b={eventStatsAthleteB[eventId]?.winCount}
                    />
                    <ComparisonRow
                        name="Longest Win Streak"
                        a={eventStatsAthleteA[eventId]?.longestWinStreak}
                        b={eventStatsAthleteB[eventId]?.longestWinStreak}
                    />
                    <ComparisonRow
                        name="Win Rate"
                        a={eventStatsAthleteA[eventId]?.winRate}
                        b={eventStatsAthleteB[eventId]?.winRate}
                        formatValueAs="percentage"
                    />
                    <ComparisonRow
                        name="Top 3 Rate"
                        a={eventStatsAthleteA[eventId]?.topThreeRate}
                        b={eventStatsAthleteB[eventId]?.topThreeRate}
                        formatValueAs="percentage"
                    />
                </>
            )
        );
    };
    return (
        <div className="head-to-head">
            <div className="athletes">
                <div className="athlete" style={{ border: "2px solid tomato" }}>
                    <AthleteInfo athlete={athleteA} />
                </div>
                <div
                    className="athlete"
                    style={{ border: "2px solid #6347ff" }}
                >
                    <AthleteInfo athlete={athleteB} />
                </div>
            </div>
            <div className="event-select">
                <select value={selectedEvent} onChange={handleSelectEvent}>
                    {athleteA &&
                        athleteB &&
                        Object.keys(comparisonSheet).length &&
                        Object.keys(
                            comparisonSheet[athleteA.id][athleteB.id]
                        ).map((eventId) => (
                            <option value={eventId}>
                                {eventId !== "ALL"
                                    ? allEvents.find(({ id }) => id === eventId)
                                          ?.name
                                    : "ALL"}
                            </option>
                        ))}
                </select>
            </div>
            <div className="comparison">
                <div className="statistics">
                    <ComparisonSection eventId={selectedEvent} />
                    {athleteA &&
                        athleteB &&
                        Object.keys(comparisonSheet).length &&
                        Object.keys(comparisonSheet[athleteA.id][athleteB.id])
                            .length === 0 &&
                        "Never competed!"}
                </div>
                <div className="results">
                    {headToHeadPerformances.length > 0 && <div>Results</div>}
                    {headToHeadPerformances.map((ps) =>
                        ps[0].eventId === selectedEvent ||
                        selectedEvent === "ALL" ? (
                            <ResultComparisonRow
                                performances={ps}
                                event={
                                    selectedEvent === "ALL"
                                        ? allEvents.find(
                                              ({ id }) => ps[0].eventId === id
                                          )?.name
                                        : undefined
                                }
                            />
                        ) : null
                    )}
                </div>
                {showSearch && (
                    <Search
                        headers={["Name", "Club"]}
                        fetchAction={fetchAthleteSearchResults}
                        dataToResultFormat={dataToResultFormat}
                        entity="athlete"
                        handleResultClick={handleResultClick}
                    />
                )}
            </div>
        </div>
    );
};

export default HeadToHeadPage;
