import { isTimedPerformance } from "./queries";

export const formatPerformance = (performance: number, event?: string) => {
    if (isTimedPerformance(event)) {
        let performanceRemainingSeconds = performance;
        const hours = Math.floor(performanceRemainingSeconds / 3600);
        performanceRemainingSeconds = performanceRemainingSeconds % 3600;
        const minutes = Math.floor(performanceRemainingSeconds / 60);
        performanceRemainingSeconds = performanceRemainingSeconds % 60;
        const seconds = performanceRemainingSeconds;
        return `${hours ? `${hours}:` : ""}${hours && minutes < 10 ? "0" : ""}${
            minutes ? `${minutes}:` : ""
        }${minutes && seconds < 10 ? "0" : ""}${
            seconds ? seconds.toFixed(hours ? 0 : 2) : ""
        }`;
    } else {
        return `${performance.toFixed(2)}`;
    }
};

export const formatPercentage = (value: number) => `${value.toFixed(0)}%`;

export const formatShortDate = (date: number) =>
    new Date(date).toLocaleString(undefined, {
        day: "2-digit",
        month: "2-digit",
        year: "2-digit",
    });

export const formatDate = (date: number) =>
    new Date(date).toLocaleString(undefined, {
        day: "2-digit",
        month: "short",
        year: "2-digit",
    });

export const formatYear = (date: number) =>
    new Date(date).toLocaleString(undefined, {
        year: "2-digit",
    });

export const formatPosition = (position: number) => {
    if (position === 11) {
        return "11th";
    }
    if (position === 12) {
        return "12th";
    }
    if (position === 13) {
        return "13th";
    }
    switch (position % 10) {
        case 1:
            return `${position}st`;
        case 2:
            return `${position}nd`;
        case 3:
            return `${position}rd`;
        default:
            return `${position}th`;
    }
};
