import { createSlice } from "@reduxjs/toolkit";
import { Athlete, Performance, Event, Meeting, Venue, Ranking } from "./types";

export interface AnalysisState {
    athletes: Athlete[];
    rankings: Ranking[];
    performances: Performance[];
    venues: Venue[];
    events: Event[];
    meetings: Meeting[];
    analysisType: AnalysisType;
    selectedAthleteIds: string[];
    error: Error | null;
}

export enum AnalysisType {
    INDIVIDUAL = "individual",
    HEAD_TO_HEAD = "head-to-head",
    GROUP = "group",
}

const initialState: AnalysisState = {
    performances: [],
    rankings: [],
    athletes: [],
    venues: [],
    events: [],
    meetings: [],
    analysisType: AnalysisType.INDIVIDUAL,
    selectedAthleteIds: [],
    error: null,
};

interface AddAction<PayloadType> {
    type: string;
    payload: PayloadType;
}

interface SetAction<PayloadType> {
    type: string;
    payload: PayloadType[];
}

const analysis = createSlice({
    name: "analysis",
    initialState,
    reducers: {
        setPerformances(state, action: SetAction<Performance>) {
            state.performances = action.payload;
        },
        addPerformances(state, action) {
            state.performances.push(...action.payload);
        },
        addRankings(state, action) {
            state.rankings.push(...action.payload);
        },
        setAthletes(state, action: SetAction<Athlete>) {
            state.athletes = action.payload;
        },
        addAthlete(state, action: AddAction<Athlete>) {
            state.athletes.push(action.payload);
        },
        removeAthlete(state, action: AddAction<Athlete>) {
            state.athletes = state.athletes.filter(
                (a) => a.id !== action.payload.id
            );
            state.performances = state.performances.filter(
                (p) => p.athleteId !== action.payload.id
            );
        },
        setVenues(state, action: SetAction<Venue>) {
            state.venues = action.payload;
        },
        setEvents(state, action: SetAction<Event>) {
            state.events = action.payload;
        },
        setMeetings(state, action: SetAction<Meeting>) {
            state.meetings = action.payload;
        },
        setError(state, action) {
            state.error = action.payload;
        },
        toggleAthleteSelection(state, action: AddAction<string>) {
            if (state.selectedAthleteIds.includes(action.payload)) {
                state.selectedAthleteIds = state.selectedAthleteIds.filter(
                    (id) => id !== action.payload
                );
            } else {
                if (state.analysisType === AnalysisType.INDIVIDUAL) {
                    state.selectedAthleteIds = [action.payload];
                } else if (state.analysisType === AnalysisType.HEAD_TO_HEAD) {
                    state.selectedAthleteIds = [
                        state.selectedAthleteIds[
                            state.selectedAthleteIds.length - 1
                        ],
                        action.payload,
                    ];
                } else {
                    state.selectedAthleteIds.push(action.payload);
                }
            }
        },
        setAnalysisType(state, action: AddAction<AnalysisType>) {
            state.analysisType = action.payload;
        },
    },
});

export const actions = analysis.actions;

export default analysis.reducer;
