import { AnalysisState } from "./slice";

export const getAthletes = (state: AnalysisState) => state.athletes;

export const getPerformances = (state: AnalysisState) => state.performances;

export const getAnalysisType = (state: AnalysisState) => state.analysisType;

export const getEvents = (state: AnalysisState) => state.events;

export const getVenues = (state: AnalysisState) => state.venues;

export const getMeetings = (state: AnalysisState) => state.meetings;

export const getError = (state: AnalysisState) => state.error;

export const getSelectedAthleteIds = (state: AnalysisState) =>
    state.selectedAthleteIds;

export const getAthleteById = (state: AnalysisState, id?: string) =>
    state.athletes.find((athlete) => athlete.id === id);

export const getRankingsByAthleteId = (state: AnalysisState, id?: string) =>
    state.rankings
        .filter(({ athleteId }) => athleteId === id)
        .sort((a, b) => parseInt(b.year) - parseInt(a.year));

export const getVenueById = (state: AnalysisState, id?: string) =>
    state.venues.find((venue) => venue.id === id);

export const getAthleteIds = (state: AnalysisState) =>
    state.athletes.map(({ id }) => id);

export const getPerformancesForIndividualAnalysis = (state: AnalysisState) =>
    state.performances.filter(
        ({ athleteId }) => athleteId === state.selectedAthleteIds[0]
    );
