import {
    formatDate,
    formatPerformance,
    formatPosition,
} from "../../../formatters";
import { Performance } from "../../../types";
import "./resultComparisonRow.css";

interface ResultComparisonRowProps {
    performances: Performance[];
    event?: string;
}

const ResultComparisonRow = ({
    performances,
    event,
}: ResultComparisonRowProps) => {
    if (performances.length !== 2) {
        return null;
    }
    return (
        <div className="result-comparison-row">
            <div className="performance">
                <div className="value">
                    {formatPerformance(performances[0].performance, event)}
                </div>
                <div className="position">
                    {formatPosition(performances[0].position)}
                </div>
            </div>
            <div className="details">
                <div className="date">{formatDate(performances[0].date)}</div>
                <div className="meeting">{performances[0].meeting}</div>
                {event && <div className="event">{event}</div>}
            </div>
            <div className="performance">
                <div className="value">
                    {formatPerformance(performances[1].performance, event)}
                </div>
                <div className="position">
                    {formatPosition(performances[1].position)}
                </div>
            </div>
        </div>
    );
};

export default ResultComparisonRow;
