import { MEDAL } from "../../constants";

const MedalTally = ({ medals }: { medals: Record<string, MEDAL[]> }) => {
    const getMedalElement = (medal: MEDAL) => {
        switch (medal) {
            case 0:
                return <div className="gold-medal">G</div>;
            case 1:
                return <div className="silver-medal">S</div>;
            case 2:
                return <div className="bronze-medal">B</div>;
            default:
                return null;
        }
    };
    return (
        <div className="athlete-medals">
            {Object.entries(medals).map(([c, ms]) => (
                <div className="championship-medals">
                    {c}
                    {ms.map((m) => (
                        <div className="medal">{getMedalElement(m)}</div>
                    ))}
                </div>
            ))}
        </div>
    );
};

export default MedalTally;
