import { useState } from "react";
import {
    formatPercentage,
    formatPerformance,
    formatPosition,
} from "../../formatters";
import { EventStatistics, Ranking, Performance } from "../../types";
import { AnalysisState } from "../../slice";
import { useSelector } from "react-redux";
import { getVenueById } from "../../selectors";
import Chart from "../../components/Chart/Chart";
import "./athleteEventCard.css";

const AthleteEventCard = ({
    name,
    statistics,
    performances,
    rankings,
    showRanking = false,
}: {
    name: string;
    statistics: EventStatistics;
    performances: Performance[];
    rankings: Record<string, Ranking>;
    showRanking?: boolean;
}) => {
    const [isExpanded, setIsExpanded] = useState(false);
    const venue = useSelector((state: AnalysisState) =>
        getVenueById(state, statistics.mostPopularVenueId)
    );
    const currentYear = new Date(performances[0]?.date)?.getFullYear();
    const hasPreviousYear = !!rankings[currentYear - 1];
    const hasCurrentYear = !!rankings[currentYear];
    const rankingTickArrow = (rankings: Record<string, Ranking>) =>
        hasPreviousYear &&
        rankings[currentYear - 1]?.rank - rankings[currentYear]?.rank > 0
            ? "\u2191"
            : "\u2193";
    const getTickColour = () => {
        if (!hasPreviousYear) {
            return undefined;
        }
        if (rankings[currentYear - 1]?.rank - rankings[currentYear]?.rank > 0) {
            return "green";
        } else if (
            rankings[currentYear - 1]?.rank - rankings[currentYear]?.rank <
            0
        ) {
            return "red";
        }
    };
    return (
        <>
            <div
                className={`athlete-event-card ${isExpanded ? "selected" : ""}`}
                onClick={() => setIsExpanded(!isExpanded)}
            >
                <div className="main-row">
                    <div className="row-left">
                        <div className="name">{name}</div>
                    </div>
                    <div className="row-right">
                        {showRanking && (
                            <div className="ranking-details">
                                <div className="current-rank">
                                    <div>
                                        {rankings[currentYear]?.rank
                                            ? formatPosition(
                                                  rankings[currentYear]?.rank
                                              )
                                            : ""}
                                    </div>
                                    <div className="rank-label">
                                        {rankings[currentYear]?.rank
                                            ? "Ranked"
                                            : "Not Ranked"}
                                    </div>
                                </div>
                                {hasPreviousYear && hasCurrentYear && (
                                    <div
                                        className="ranking-tick"
                                        style={{ color: getTickColour() }}
                                    >
                                        {rankings[currentYear - 1]?.rank -
                                            rankings[currentYear]?.rank !==
                                        0
                                            ? `${rankingTickArrow(
                                                  rankings
                                              )}${Math.abs(
                                                  rankings[currentYear - 1]
                                                      ?.rank -
                                                      rankings[currentYear]
                                                          ?.rank
                                              )}`
                                            : null}
                                    </div>
                                )}
                            </div>
                        )}
                        <div
                            className="pb"
                            style={
                                showRanking
                                    ? {}
                                    : { width: "100%", textAlign: "right" }
                            }
                        >
                            {formatPerformance(statistics.best, name)}
                        </div>
                    </div>
                </div>
                <div className="bottom-row">
                    <div className="card-expand-collapse-button">
                        {isExpanded ? "Collapse" : "Expand"}
                    </div>
                </div>
            </div>
            <div
                className={`event-card-expanded ${isExpanded ? "" : "hidden"}`}
            >
                {isExpanded && (
                    <div className="expanded-statistics">
                        <div className="statistic-row">
                            <div>Win Rate:</div>
                            <div>{formatPercentage(statistics.winRate)}</div>
                        </div>
                        <div className="statistic-row">
                            <div>Top Three Rate:</div>
                            <div>
                                {formatPercentage(statistics.topThreeRate)}
                            </div>
                        </div>
                        <div className="statistic-row">
                            <div>Win Count:</div>
                            <div>{statistics.winCount}</div>
                        </div>
                        <div className="statistic-row">
                            <div>Longest Win Streak:</div>
                            <div>{statistics.longestWinStreak}</div>
                        </div>
                        <div className="statistic-row">
                            <div>Most Common Venue:</div>
                            <div>{venue?.name}</div>
                        </div>
                        <div className="statistic-row">
                            <div>Total Performances:</div>
                            <div>{statistics.totalPerformances}</div>
                        </div>
                        <div>Progression Chart</div>
                        <Chart
                            event={name}
                            performances={performances}
                            isForSeason={showRanking}
                        />
                    </div>
                )}
            </div>
        </>
    );
};

export default AthleteEventCard;
