import { formatPerformance } from "../../../formatters";
import { Performance, Athlete } from "../../../types";
import "./performance-row.css";

const PerformanceRow = ({
    performance,
    athlete,
    isChampionship = false,
}: {
    performance: Performance;
    athlete: Athlete;
    isChampionship: boolean;
}) => {
    const getMedalElement = (position: number) => {
        switch (position) {
            case 1:
                return <div className="gold-medal position">G</div>;
            case 2:
                return <div className="silver-medal position">S</div>;
            case 3:
                return <div className="bronze-medal position">B</div>;
            default:
                return null;
        }
    };
    return (
        <div className="performance-row">
            {isChampionship &&
            performance.position < 4 &&
            performance.heat === "" &&
            performance.position ? (
                getMedalElement(performance.position)
            ) : (
                <div className="position">
                    {Number.isNaN(performance.position)
                        ? "-"
                        : performance.position}
                </div>
            )}
            <div className="athlete">{athlete.name}</div>
            <div className="performance">
                {performance.performance
                    ? formatPerformance(performance.performance)
                    : "-"}
            </div>
        </div>
    );
};

export default PerformanceRow;
