import { useState } from "react";
import { Link } from "react-router-dom";

export const MenuIcon = () => (
    <div className="menu-icon">
        <div className="bar"></div>
        <div className="bar"></div>
        <div className="bar"></div>
    </div>
);

const HeaderMenu = () => {
    const [showMenuContents, setShowMenuContents] = useState(false);

    const toggleShowMenuContents = () => {
        setShowMenuContents(!showMenuContents);
    };

    return (
        <div className="header-menu" onClick={toggleShowMenuContents}>
            <MenuIcon />
            <div className={`menu-content ${showMenuContents ? "" : "hide"}`}>
                <Link className="menu-item" to="/">
                    Home
                </Link>
                <Link className="menu-item" to="/athlete">
                    Athlete
                </Link>
                <Link className="menu-item" to="/meeting">
                    Meeting
                </Link>
                <Link className="menu-item" to="/headtohead">
                    Head To Head
                </Link>
                <Link className="menu-item" to="/news">
                    News
                </Link>
                <Link className="menu-item" to="/contact">
                    Contact
                </Link>
            </div>
            <div
                className={`app-menu-blanket ${
                    showMenuContents ? "" : "hidden"
                }`}
            ></div>
        </div>
    );
};

export default HeaderMenu;
