import { store } from "./index";
import {
    getAthleteData,
    getAthleteSearchResults,
    getMeetingSearchResults,
    getMeetingData,
} from "./scraper";
import { actions } from "./slice";
import { Athlete, Event, Meeting, Venue, Performance } from "./types";

export const fetchAthleteData = async (
    id: string,
    events: Event[],
    meetings: Meeting[],
    venues: Venue[]
) => {
    try {
        const data = await getAthleteData(id, events, meetings, venues)
            .then((response) => response)
            .catch((error: Error) => {
                throw error;
            });
        if (data === null) {
            return;
        }
        store.dispatch(actions.addAthlete(data.athlete));
        store.dispatch(actions.addPerformances(data.performances));
        store.dispatch(actions.setVenues(data.venues));
        store.dispatch(actions.setEvents(data.events));
        store.dispatch(actions.setMeetings(data.meetings));
        store.dispatch(actions.addRankings(data.rankings));
        store.dispatch(actions.setError(null));
    } catch (error) {
        store.dispatch(actions.setError(error));
    }
};

export const fetchMeetingData = async (id: string, events: Event[]) => {
    try {
        const data: {
            performances: Performance[];
            athletes: Athlete[];
            events: Event[];
            details: any;
        } = {
            performances: [],
            athletes: [],
            events: [],
            details: undefined,
        };
        const firstData = await getMeetingData(id, events, 1)
            .then((response) => response)
            .catch((error: Error) => {
                throw error;
            });
        if (firstData === null) {
            return;
        }
        data.performances.push(...firstData.performances);
        data.events = firstData.events;
        data.athletes.push(...firstData.athletes);
        data.details = firstData.details;
        if (firstData.numberOfPages > 1) {
            // data.push(firstData);
            for (let page = 2; page <= firstData.numberOfPages; page++) {
                await getMeetingData(
                    id,
                    data.events,
                    page,
                    data.athletes.map(({ id }) => id) // create athleteIds array to pass
                )
                    .then((response) => {
                        if (response) {
                            data.performances.push(...response.performances);
                            data.events = response.events;
                            data.athletes.push(...response.athletes);
                        }
                    })
                    .catch((error: Error) => {
                        throw error;
                    });
            }
        }
        // spread each page's data into all of these
        store.dispatch(actions.setPerformances(data.performances));
        store.dispatch(actions.setAthletes(data.athletes));
        store.dispatch(actions.setEvents(data.events));
        store.dispatch(actions.setMeetings([data.details]));
    } catch (error) {
        store.dispatch(actions.setError(error));
    }
};

export const fetchAthleteSearchResults = async (
    surname: string,
    firstname: string,
    club: string
) => {
    try {
        const data = await getAthleteSearchResults(surname, firstname, club)
            .then((response) => response)
            .catch((error: Error) => {
                throw error;
            });

        if (data === null) {
            return [];
        }

        if (!Array.isArray(data)) {
            store.dispatch(actions.addAthlete(data.athlete));
            store.dispatch(actions.addPerformances(data.performances));
            store.dispatch(actions.setVenues(data.venues));
            store.dispatch(actions.setEvents(data.events));
            store.dispatch(actions.setMeetings(data.meetings));
            store.dispatch(actions.addRankings(data.rankings));
            store.dispatch(actions.setError(null));
            return [
                {
                    id: data.athlete.id,
                    name: data.athlete.name,
                    sex: data.athlete.sex,
                    ageGroup: data.athlete.ageGroup,
                    club: data.athlete.club,
                },
            ];
        }

        return data;
    } catch (error) {
        store.dispatch(actions.setError(error));
        return [];
    }
};

export const fetchMeetingSearchResults = async () => {
    const data = await getMeetingSearchResults()
        .then((response) => response.sort((a, b) => b.date - a.date))
        .catch((error: Error) => {
            throw error;
        });
    return data;
};
