import Result, { SearchResultType } from "./Result";

const Results = ({
    results,
    entity,
    handleResultClick,
}: {
    results: SearchResultType[];
    entity: string;
    handleResultClick?: (id: string) => () => void;
}) => {
    return (
        <div className="results">
            {results.map(({ id, primary, secondary, sub }) => (
                <Result
                    id={id}
                    key={id}
                    primary={primary}
                    secondary={secondary}
                    sub={sub}
                    entity={entity}
                    onResultClick={
                        handleResultClick ? handleResultClick(id) : undefined
                    }
                />
            ))}
        </div>
    );
};

export default Results;
