import { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { useParams } from "react-router-dom";
import LoadingIcon from "../../components/LoadingIcon/LoadingIcon";
import {
    getAthletes,
    getEvents,
    getMeetings,
    getPerformances,
} from "../../selectors";
import { Athlete, Meeting, Performance } from "../../types";
import { fetchMeetingData } from "../../thunks";
import PerformanceRow from "../../components/Rows/PerformanceRow/PerformanceRow";
import { CHAMPIONSHIPS } from "../../constants";
import "./meeting.css";

const MeetingPage = () => {
    const { id } = useParams();
    const allPerformances = useSelector(getPerformances);
    const allAthletes = useSelector(getAthletes);
    const allEvents = useSelector(getEvents);
    const allMeetings = useSelector(getMeetings);
    const [meeting, setMeeting] = useState<Meeting | undefined>(undefined);
    const [performances, setPerformances] = useState<Performance[]>([]);
    const [isChampionship, setIsChampionship] = useState(false);
    const [eventFilter, setEventFilter] = useState("ALL");
    const [showOnlyFinals, setShowOnlyFinals] = useState(false);

    const performanceSort = (a: Performance, b: Performance) => {
        if (a.eventId === b.eventId) {
            if (a.tags.includes("para") !== b.tags.includes("para")) {
                return -1;
            }
            if ((a?.classification ?? "") === (b?.classification ?? "")) {
                if (a.heat === b.heat) {
                    if (Number.isNaN(b.position)) {
                        return -1;
                    }
                    return a.position < b.position ? -1 : 1;
                }
                return a.heat < b.heat ? -1 : 1; // heat sorting needs preference object
            }
            return (a?.classification ?? "") < (b?.classification ?? "")
                ? -1
                : 1;
        }
        return a.eventId < b.eventId ? -1 : 1;
    };

    const performanceFilter = (p: Performance) =>
        (p.eventId === eventFilter || eventFilter === "ALL") &&
        (!showOnlyFinals || p.heat === "");

    const handleEventFilterChange: React.ChangeEventHandler<
        HTMLSelectElement
    > = (event) => {
        setEventFilter(event.target.value);
    };

    useEffect(() => {
        if (id) {
            fetchMeetingData(id, allEvents);
            setIsChampionship(Object.values(CHAMPIONSHIPS).flat().includes(id));
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [id]);

    useEffect(() => {
        setPerformances(allPerformances.filter((p) => p.meetingId === id));
    }, [allPerformances, id]);

    useEffect(() => {
        setMeeting(allMeetings.find((m) => m.id === id));
    }, [allMeetings, id]);

    if (!meeting) {
        return <LoadingIcon />;
    }

    const TableHeader = ({
        performance,
        isChampionship = false,
    }: {
        performance: Performance;
        isChampionship?: boolean;
    }) => (
        <div className="table-header">
            <div className="event">
                {allEvents.find(({ id }) => performance.eventId === id)?.name}
            </div>
            <div>
                {isChampionship && performance.heat === ""
                    ? "Final"
                    : performance.heat}
            </div>
            <div>{performance.classification}</div>
        </div>
    );

    return (
        <div className="meeting-page">
            <div className="meeting-name">{meeting?.name}</div>
            <div className="meeting-details">
                <div className="meeting-location">{meeting?.location}</div>
                <div className="meeting-date">{meeting?.date}</div>
            </div>
            <div className="filter-panel">
                Filter by:
                <div className="event-filter">
                    Event:
                    <select name="event" onChange={handleEventFilterChange}>
                        <option value="ALL">ALL</option>
                        {allEvents.map((e) => (
                            <option value={e.id}>{e.name}</option>
                        ))}
                    </select>
                </div>
                {isChampionship && (
                    <div>
                        Finals Only
                        <input
                            className="finals-only"
                            type="checkbox"
                            onClick={() => {
                                setShowOnlyFinals(!showOnlyFinals);
                            }}
                        />
                    </div>
                )}
            </div>
            <div className="performances">
                {performances
                    .filter(performanceFilter)
                    .sort(performanceSort)
                    .map((p) => (
                        <>
                            {p.position === 1 && (
                                <TableHeader
                                    performance={p}
                                    isChampionship={isChampionship}
                                />
                            )}
                            <PerformanceRow
                                performance={p}
                                athlete={
                                    allAthletes.find(
                                        (a) => a.id === p.athleteId
                                    ) as Athlete
                                }
                                isChampionship={isChampionship}
                            />
                        </>
                    ))}
            </div>
        </div>
    );
};

export default MeetingPage;
