import "./news.css";

const NewsPage = () => {
    const sites = [
        { name: "BBC Sport", link: "https://www.bbc.co.uk/sport/athletics" },
        {
            name: "Guardian",
            link: "https://www.theguardian.com/sport/athletics",
        },
        {
            name: "Independent",
            link: "https://www.independent.co.uk/sport/general/athletics",
        },
    ];

    return (
        <div className="news-page">
            {sites.map((site) => (
                <a href={site.link} className="news-site-card">
                    {site.name}
                </a>
            ))}
        </div>
    );
};

export default NewsPage;
