import { SearchResultType } from "../../components/Search/Result";
import Search from "../../components/Search/Search";
import { fetchMeetingSearchResults } from "../../thunks";
import { formatDate } from "../../formatters";

const MeetingSearch = () => {
    const meetingFormatter = (
        data: { id: string; name: string; venueName: string; date: number }[]
    ): SearchResultType[] =>
        data.map((d) => ({
            id: d.id,
            primary: d.name,
            secondary: d.venueName,
            sub: formatDate(d.date),
        }));
    return (
        <div className="meeting-search">
            <Search
                headers={["Name", "Venue"]}
                fetchAction={fetchMeetingSearchResults}
                dataToResultFormat={meetingFormatter}
                isImmediateSearch={true}
                entity="meeting"
            />
        </div>
    );
};

export default MeetingSearch;
