import { Performance } from "./types";

const getScoreSheetForAthlete = (
    athleteIds: string[]
): Record<string, Object> =>
    athleteIds.reduce((sheet, id) => ({ ...sheet, [id]: { ALL: 0 } }), {});

const getScoreSheet = (athleteIds: string[]) =>
    athleteIds.reduce(
        (sheet, id) => ({
            ...sheet,
            [id]: getScoreSheetForAthlete(
                athleteIds.filter((idB) => idB !== id)
            ),
        }),
        {}
    );

const getPerformancesByAthlete = (
    performances: Performance[],
    athleteIds: string[]
) => {
    const performancesByAthlete: Record<string, Performance[]> =
        athleteIds.reduce(
            (arrangedPerformances, id) => ({
                [id]: [],
                ...arrangedPerformances,
            }),
            {}
        );

    const toReturn = performances.reduce((pByA, p) => {
        if (pByA[p.athleteId]) {
            pByA[p.athleteId].push(p);
        }
        return pByA;
    }, performancesByAthlete);

    athleteIds.forEach((id) => {
        toReturn[id].sort((a, b) => b.date - a.date);
    });

    return toReturn;
};

const scorePointsForMatchedEvent = (
    scoreSheet: Record<string, Record<string, Record<string, number>>>,
    eventId: string,
    idA: string,
    idB: string
) => {
    if (scoreSheet[idA][idB][eventId] !== undefined) {
        scoreSheet[idA][idB][eventId]++;
    } else {
        scoreSheet[idA][idB][eventId] = 1;
        scoreSheet[idB][idA][eventId] = 0;
    }
    scoreSheet[idA][idB].ALL++;
};

const isSameRace = (performanceA: Performance, performanceB: Performance) =>
    performanceA.meetingId === performanceB.meetingId &&
    performanceA.eventId === performanceB.eventId &&
    performanceA.heat === performanceB.heat;

export const getAthleteHeadToHead = (
    athleteIds: string[],
    performances: Performance[]
): Record<string, Record<string, Record<string, number>>> => {
    const scoreSheet = getScoreSheet(athleteIds);
    const performancesByAthlete = getPerformancesByAthlete(
        performances,
        athleteIds
    );

    for (let i = 0; i < athleteIds.length - 1; i++) {
        for (let p = 0; p < performancesByAthlete[athleteIds[i]].length; p++) {
            const pToCheck = performancesByAthlete[athleteIds[i]][p];
            for (let j = i + 1; j < athleteIds.length; j++) {
                if (i === j) {
                    break;
                }
                for (
                    let q = 0;
                    q < performancesByAthlete[athleteIds[j]].length;
                    q++
                ) {
                    // don't need to check if we are already "passed" that event
                    if (
                        pToCheck.date >
                        performancesByAthlete[athleteIds[j]][q].date
                    ) {
                        break;
                    }
                    if (
                        isSameRace(
                            pToCheck,
                            performancesByAthlete[athleteIds[j]][q]
                        )
                    ) {
                        if (
                            pToCheck.position <
                            performancesByAthlete[athleteIds[j]][q].position
                        ) {
                            scorePointsForMatchedEvent(
                                scoreSheet,
                                pToCheck.eventId,
                                athleteIds[i],
                                athleteIds[j]
                            );
                        } else {
                            scorePointsForMatchedEvent(
                                scoreSheet,
                                pToCheck.eventId,
                                athleteIds[j],
                                athleteIds[i]
                            );
                        }
                        break;
                    }
                }
            }
        }
    }
    return scoreSheet;
};

export const getHeadToHeadPerformances = (
    athleteIds: string[],
    performances: Performance[]
): Performance[][] => {
    const headToHeadPerformances: Performance[][] = [];
    const performancesByAthlete = getPerformancesByAthlete(
        performances,
        athleteIds
    );
    for (let i = 0; i < athleteIds.length - 1; i++) {
        for (let p = 0; p < performancesByAthlete[athleteIds[i]].length; p++) {
            const pToCheck = performancesByAthlete[athleteIds[i]][p];
            for (let j = i + 1; j < athleteIds.length; j++) {
                if (i === j) {
                    break;
                }
                for (
                    let q = 0;
                    q < performancesByAthlete[athleteIds[j]].length;
                    q++
                ) {
                    // don't need to check if we are already "passed" that event
                    if (
                        pToCheck.date >
                        performancesByAthlete[athleteIds[j]][q].date
                    ) {
                        break;
                    }
                    if (
                        isSameRace(
                            pToCheck,
                            performancesByAthlete[athleteIds[j]][q]
                        )
                    ) {
                        headToHeadPerformances.push([
                            pToCheck,
                            performancesByAthlete[athleteIds[j]][q],
                        ]);
                        break;
                    }
                }
            }
        }
    }
    return headToHeadPerformances;
};
