import { useParams } from "react-router-dom";
import { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import LoadingIcon from "../../components/LoadingIcon/LoadingIcon";
import AthleteEventCard from "./AthleteEventCard";
import MedalTally from "./MedalTally";
import { fetchAthleteData } from "../../thunks";
import {
    getAthleteById,
    getAthleteIds,
    getEvents,
    getMeetings,
    getPerformances,
    getRankingsByAthleteId,
    getVenues,
} from "../../selectors";
import { AnalysisState } from "../../slice";
import {
    getChampionshipMedals,
    getEventStatistics,
    getPerformancesByEvent,
} from "../../queries";
import { Performance, Event, EventStatistics, Ranking } from "../../types";
import { MEDAL } from "../../constants";
import SectionsCarousel from "./SectionsCarousel";
import AthletePagePerformanceRow from "./AthletePagePerformanceRow";
import "./athlete.css";

const AthletePage = () => {
    const { id } = useParams();
    const allEvents = useSelector(getEvents);
    const allMeetings = useSelector(getMeetings);
    const allVenues = useSelector(getVenues);
    const athlete = useSelector((state: AnalysisState) =>
        getAthleteById(state, id)
    );
    const allPerformances = useSelector(getPerformances);
    const rankings = useSelector((state: AnalysisState) =>
        getRankingsByAthleteId(state, id)
    );
    const [performances, setPerformances] = useState<Performance[]>([]);
    const [events, setEvents] = useState<Event[]>([]);
    const fetchedAthleteIds = useSelector(getAthleteIds);
    const [eventStats, setEventStats] = useState<
        Record<string, EventStatistics>
    >({});
    const [medals, setMedals] = useState<Record<string, MEDAL[]>>({});
    const [overall, setOverall] = useState(false);
    const [season, setSeason] = useState(true);
    const [results, setResults] = useState(false);
    const [seasonYear, setSeasonYear] = useState(
        new Date(Date.now()).getFullYear()
    );
    useEffect(() => {
        if (
            id &&
            !fetchedAthleteIds.includes(id) &&
            allEvents !== undefined &&
            allVenues !== undefined &&
            allMeetings !== undefined
        ) {
            fetchAthleteData(id, allEvents, allMeetings, allVenues);
        }
    }, [id, allEvents, allMeetings, allVenues, fetchedAthleteIds]);
    useEffect(() => {
        setPerformances(
            allPerformances.filter(
                (p) =>
                    p.athleteId === id &&
                    (new Date(p.date).getFullYear() === seasonYear || !season)
            )
        );
        setMedals(
            getChampionshipMedals(
                allPerformances.filter((p) => p.athleteId === id)
            )
        );
    }, [allPerformances, id, season, seasonYear]);
    useEffect(() => {
        if (performances.length > 0) {
            setEvents(
                allEvents.reduce<Event[]>(
                    (acc, e) =>
                        performances.findIndex((p) => e.id === p.eventId) > -1
                            ? [...acc, e]
                            : acc,
                    []
                )
            );
        }
    }, [performances, allEvents]);
    useEffect(() => {
        if (performances.length && events.length) {
            setEventStats(
                getEventStatistics(getPerformancesByEvent(performances), events)
            );
        }
    }, [performances, events]);

    const SeasonOverview = ({ events }: { events: Event[] }) => {
        if (events.length === 0 || Object.keys(eventStats).length === 0) {
            return null;
        }
        const yearsCompetedIn = allPerformances
            .filter((p) => p.athleteId === id)
            .reduce((list: number[], p) => {
                const thisPsYear = new Date(p.date).getFullYear();
                return list.includes(thisPsYear) ? list : [...list, thisPsYear];
            }, []);
        return (
            <>
                <div className="season-selector">
                    {yearsCompetedIn.map((year) => (
                        <div
                            className={`season-year ${
                                seasonYear === year ? "selected" : ""
                            } `}
                            onClick={() => setSeasonYear(year)}
                        >
                            {year}
                        </div>
                    ))}
                </div>
                {events
                    .sort(
                        (eventA, eventB) =>
                            performances.filter((p) => p.eventId === eventB.id)
                                .length -
                            performances.filter((p) => p.eventId === eventA.id)
                                .length
                    )
                    .map((event) => (
                        <AthleteEventCard
                            name={event.name}
                            statistics={eventStats[event.id]}
                            performances={performances.filter(
                                (p) => p.eventId === event.id
                            )}
                            rankings={rankings.reduce(
                                (acc, r): Record<string, Ranking> => {
                                    if (event.id === r.eventId) {
                                        return { [r.year]: r, ...acc };
                                    }
                                    return acc;
                                },
                                {}
                            )}
                            showRanking={true}
                        />
                    ))}
            </>
        );
    };

    const OverallOverview = ({ events }: { events: Event[] }) => {
        if (events.length === 0 || Object.keys(eventStats).length === 0) {
            return null;
        }
        return (
            <>
                {events
                    .sort(
                        (eventA, eventB) =>
                            performances.filter((p) => p.eventId === eventB.id)
                                .length -
                            performances.filter((p) => p.eventId === eventA.id)
                                .length
                    )
                    .map((event) => (
                        <AthleteEventCard
                            name={event.name}
                            statistics={eventStats[event.id]}
                            performances={performances.filter(
                                (p) => p.eventId === event.id
                            )}
                            rankings={rankings.reduce(
                                (acc, r): Record<string, Ranking> => {
                                    if (event.id === r.eventId) {
                                        return { [r.year]: r, ...acc };
                                    }
                                    return acc;
                                },
                                {}
                            )}
                        />
                    ))}
            </>
        );
    };

    const PerformancesList = ({
        performances,
    }: {
        performances: Performance[];
    }) => {
        return (
            <>
                {performances.map((p) => (
                    <AthletePagePerformanceRow
                        performance={p}
                        eventName={
                            allEvents.find((e) => e.id === p.eventId)?.name
                        }
                        venue={allVenues.find((v) => v.id === p.venueId)}
                    />
                ))}
            </>
        );
    };

    return (
        <>
            {performances.length === 0 && athlete === undefined && (
                <LoadingIcon />
            )}
            <div className="athlete-page">
                {performances.length > 0 && athlete !== undefined && (
                    <>
                        <div className="athlete-details">
                            <div className="name">{athlete.name}</div>
                            <div>
                                <div className="club">{athlete.club}</div>
                                <div className="sub-details">
                                    <div className="age-group">
                                        {athlete.ageGroup}
                                    </div>
                                    <div className="sex">{athlete.sex}</div>
                                </div>
                            </div>
                        </div>
                        <MedalTally medals={medals} />
                        <div className="athlete-stats">
                            <SectionsCarousel
                                overall={overall}
                                season={season}
                                results={results}
                                setOverall={setOverall}
                                setSeason={setSeason}
                                setResults={setResults}
                            />
                            <div className="statistics-content">
                                {overall && <OverallOverview events={events} />}
                                {season && <SeasonOverview events={events} />}
                                {results && (
                                    <PerformancesList
                                        performances={performances}
                                    />
                                )}
                            </div>
                        </div>
                    </>
                )}
            </div>
        </>
    );
};

export default AthletePage;
