import * as d3 from "d3";
import { useEffect } from "react";
import { Performance } from "../../types";
import { formatPerformance, formatYear } from "../../formatters";
import { COLOURS } from "../../constants";
import {
    getBestPerformanceProgression,
    getSeasonPerformanceProgression,
} from "../../queries";
import "./chart.css";

interface Props {
    event: string;
    performances: Performance[];
    isForSeason?: boolean;
}

const Chart = ({ event, performances, isForSeason = false }: Props) => {
    const eventAsSelector = event.replaceAll(".", "");
    const plotMargins = { x: 35, y: 20 };
    useEffect(() => {
        if (performances.length) {
            drawGraph(performances);
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    });

    // const getLineData = (performances: Performance[]) =>
    //     performances
    //         .sort((a, b) => a.date - b.date)
    //         .reduce((acc: number[][], p, i) => [...acc, [p.date, i]], []);

    const drawGraph = (performances: Performance[]) => {
        // @ts-ignore
        const { width, height } = d3
            .select(`.chart-container`)
            .node()
            // @ts-ignore
            .getBoundingClientRect();
        const dateMax = d3.max(
            performances,
            (d: Performance) => d.date
        ) as number;
        const dateMin = d3.min(
            performances,
            (d: Performance) => d.date
        ) as number;
        const performanceMax = d3.max(
            performances,
            (d: Performance) => d.performance
        ) as number;
        const performanceMin = d3.min(
            performances,
            (d: Performance) => d.performance
        ) as number;

        // define our scaling functions
        const xScale = d3
            .scaleLinear()
            .domain([dateMin, dateMax])
            .range([plotMargins.x, width - plotMargins.x]);
        const yScale = d3
            .scaleLinear()
            .domain([performanceMin, performanceMax])
            .range([height - plotMargins.y, 0]);

        const graph = d3.select(`#event-chart-${eventAsSelector}`);
        graph.selectAll("*").remove();
        graph.attr("width", width).attr("height", height);
        const xAxis = d3
            .axisBottom(xScale)
            .ticks(
                new Date(dateMax).getFullYear() -
                    new Date(dateMin).getFullYear()
            )
            .tickFormat((d, i) => (i % 2 ? formatYear(d as number) : ""));
        const yAxis = d3
            .axisLeft(yScale)
            .ticks(5)
            .tickFormat((d, i) => formatPerformance(d as number).slice(0, -3));

        // add axes
        graph
            .append("g")
            .attr("transform", `translate(0, ${height - plotMargins.y})`)
            .call(xAxis)
            .selectAll("text")
            .attr("x", plotMargins.x)
            .attr("y", plotMargins.y);

        graph
            .append("g")
            .attr("transform", `translate(${plotMargins.x},0)`)
            .call(yAxis);

        graph
            .selectAll(".performance")
            .data(performances)
            .enter()
            .append("circle")
            .attr("r", 1.5)
            .attr("cx", (d) => xScale(d.date))
            .attr("cy", (d) => yScale(d.performance))
            .attr("class", "performance")
            .attr("fill", "black")
            .exit()
            .remove();

        // if (showCumulativeWins) {
        //     graph
        //         .append("path")
        //         .data([getLineData(performances)])
        //         .attr("stroke", "steelblue")
        //         .attr("stroke-width", 1.5)
        //         .attr("fill", "none")
        //         .attr(
        //             "d",
        //             // @ts-ignore
        //             d3
        //                 .line()
        //                 .x((d) => xScale(d[0]))
        //                 .y((d) => yScaleCount(d[1]))
        //         );
        //     graph
        //         .append("path")
        //         .data([
        //             getLineData(
        //                 performances.filter(({ position }) => position < 4)
        //             ),
        //         ])
        //         .attr("stroke", COLOURS.TL_AMBER)
        //         .attr("stroke-width", 1.5)
        //         .attr("fill", "none")
        //         .attr(
        //             "d",
        //             // @ts-ignore
        //             d3
        //                 .line()
        //                 .x((d) => xScale(d[0]))
        //                 .y((d) => yScaleCount(d[1]))
        //         );
        //     graph
        //         .append("path")
        //         .data([
        //             getLineData(
        //                 performances.filter(({ position }) => position === 1)
        //             ),
        //         ])
        //         .attr("stroke", COLOURS.TL_GREEN)
        //         .attr("stroke-width", 1.5)
        //         .attr("fill", "none")
        //         .attr(
        //             "d",
        //             // @ts-ignore
        //             d3
        //                 .line()
        //                 .x((d) => xScale(d[0]))
        //                 .y((d) => yScaleCount(d[1]))
        //         );
        // }
        if (true) {
            getSeasonPerformanceProgression(performances, event).forEach(
                (season) => {
                    graph
                        .append("path")
                        .data([season])
                        .attr("stroke", COLOURS.TL_AMBER)
                        .attr("stroke-width", 1.5)
                        .attr("fill", "none")
                        .attr(
                            "d",
                            // @ts-ignore
                            d3
                                .line()
                                .x((d) => xScale(d[0]))
                                .y((d) => yScale(d[1]))
                        );
                }
            );
            graph
                .append("path")
                .data([getBestPerformanceProgression(performances, event)])
                .attr("stroke", COLOURS.TL_GREEN)
                .attr("stroke-width", 1.5)
                .attr("fill", "none")
                .attr(
                    "d",
                    // @ts-ignore
                    d3
                        .line()
                        .x((d) => xScale(d[0]))
                        .y((d) => yScale(d[1]))
                );
        }
    };

    return (
        <div className="chart-container">
            <div className="svg">
                <svg
                    id={`event-chart-${eventAsSelector}`}
                    className="chart"
                ></svg>
            </div>
            {/* <div className="chart-controls">
                <div onClick={toggleShowCumulativeWins}>
                    {showCumulativeWins ? "Hide Win Count" : "Show Win Count"}
                </div>
                <div onClick={toggleShowPBProgress}>
                    {showPBProgress ? "Hide PB" : "Show PB"}
                </div>
            </div> */}
            <div className="chart-key">
                <div className="key">
                    <div className="key-descriptor">
                        {isForSeason ? "SB" : "PB"}
                    </div>
                    <div className="key-colour line pb"></div>
                </div>
                {!isForSeason && (
                    <div className="key">
                        <div className="key-descriptor">SB</div>
                        <div className="key-colour line sb"></div>
                    </div>
                )}
                <div className="key">
                    <div className="key-descriptor">Performance</div>
                    <div className="key-colour point performance"></div>
                </div>
            </div>
        </div>
    );
};
export default Chart;
